nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #53a8ee;
  padding: 20px 80px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
}
#Navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}
#Navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}
#Navbar li a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  transition: 0.3s ease-in-out;
}
#Navbar li a:hover,
#Navbar li a.active {
  color: #2a0566;
}
#Navbar li a:hover::after,
#Navbar li a.active::after {
  content: "";
  width: 30px;
  height: 2px;
  background: #2a0566;
  position: absolute;
  bottom: -4px;
  left: 20px;
}
#mobile {
  display: none;
}
#mobile i {
  color: #fff;
  align-items: center;
}

@media screen and (max-width: 769px) {
  #Navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: -300px;
    width: 300px;
    height: 100vh;
    background-color: #53a8ee;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
    padding: 40px 0 0 10px;
    transition: 0.3s ease-in-out;
  }
  #Navbar .active {
    right: 0;
  }
  #Navbar li {
    margin-bottom: 25px;
  }
  #mobile {
    display: block;
  }
  #mobile i {
    font-size: 24px;
    cursor: pointer;
  }
}
